import React from 'react';
import './App.css';
import { oauth2 as SMART } from 'fhirclient';

class App extends React.Component {

  smartKey;
  _storageData = null;
  get StorageData() {
    this._storageData = this._storageData == null ? JSON.parse(sessionStorage[this.smartKey]) : this._storageData;
    return this._storageData;
  }

  constructor (props) {
    super(props);

    this.smartKey = props.smartKey;

    console.log("props", props);
    console.log("smart", SMART);
    console.log("storage", sessionStorage);
    console.log("storage", this.StorageData);
    
    console.log("smartKey", this.smartKey);

    SMART.init({
      iss: this.StorageData["serverUrl"],
      redirectUri: this.StorageData["redirectUri"],
      clientId: this.StorageData["clientId"],
      scope: this.StorageData["scope"]
    })
    .then(client => {
      // Fetch MedicationRequest and Patient in parallel to load the app faster
      return Promise.all([
          client.patient.read(),
          client.request(`/MedicationRequest?patient=${client.patient.id}`, {
              resolveReferences: "medicationReference",
              pageLimit: 0,
              flat: true
          })
      ]);
    })
    .then(
      ([patient, meds]) => {
        console.log(JSON.stringify(patient) + "<br><br>" +  JSON.stringify(meds));
      },
      error => {
          console.error(error);
      }
    );

    // FHIR.oauth2.ready()
    // .then(client => client.request("metadata"))
    // .then(console.log)
    // .catch(console.error);
  } 

  render () {
    return <div className="App">
            Redirected to here
            {window.location.href}
            'client_id': 'ba2b34f9-2f17-4858-b5f9-6a5b3312e26a'
          </div>;
  }
}

export default App;
